import React from 'react';

const NotFound = () => {
  return (
    <div className="NotFound">
      <h1>404 Not Found</h1>
      <h3>The page you are looking for does not exist</h3>
      <h5><a href="/">Return home</a></h5>
    </div>
  );
}

export default NotFound;



